.carousel-inner{
    max-width: 90%;
    margin:0 auto;
    max-height: 100%;
}
.carousel-inner>.carousel-item{
    max-width: 100%;
    max-height: 100%;
}
.carousel-inner>.carousel-item>img:nth-child(2){
    max-width: 60vw;
    max-height: 100%;
}
::selection{
    background-color: grey;
}